body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.input-container {
    background-color: white;
    margin-top: 2rem;
    border-radius: 0.25rem;
    padding: 1rem;
    box-shadow: 0.1rem 0.1rem 1rem rgba(0, 0, 0, 0.329);
    /*display: flex;*/
    justify-content: space-between;
    height: 15vh;
}

.input {
    border: none;
    width: 90%
}

.input:focus {
    outline: none;
}

.add-btn {
    border: none;
    padding: 0.3rem 2rem;
    border-radius: 0.2rem;
    background-color: rgb(53, 201, 157);
    color: white;
    font-weight: 900;
}
input {
    border: 1px solid #999;
    padding: 1rem;
    width: 100%;
  }
  
  .no-suggestions {
    color: #999;
    padding: 0.5rem;
  }
  
  .suggestions {
    border: 1px solid #999;
    border-top-width: 0;
    list-style: none;
    margin-top: 0;
    max-height:300px;
    overflow-y: auto;
    padding-left: 0;
    width: 100%;
  }
  
  .suggestions li {
    padding: 0.5rem;
  }
  
  .suggestion-active,
  .suggestions li:hover {
    background-color: #008f68;
    color: #fff;
    cursor: pointer;
    font-weight: 700;
  }
  
  .suggestions li:not(:last-of-type) {
    border-bottom: 1px solid #999;
  }
.header {
    color: white;
    letter-spacing: 0.5rem;
    
    font-size: 2rem;
    text-shadow: 0.1rem 0.1rem 1rem rgba(0, 0, 0, 0.342);
}
.todolist-container {
    background-color: white;
    margin-top: 2rem;
    border-radius: 0.25rem;
    padding: 1rem;
    box-shadow: 0.1rem 0.1rem 1rem rgba(0, 0, 0, 0.329);
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 50vh;
}

.todos-container {
  
    height: 90%;
    
}

.todo-item {
    border-bottom: .05rem solid rgba(0, 0, 0, 0.082);
    padding: 1rem 0;
    cursor: pointer;
    width: 50%;
}

.todo-item-active {
    
    color: grey
}

.postcode-container {
    width: 100%;    
    /*border: 1px #000 solid;*/
    margin: 15px 0px 15px 0px;
   
}

.grid-striped .row:nth-of-type(odd) {
    background-color: rgba(0,0,0,.05);
    
}

.map-container {
    height: 250px;
    width: 100%;    
    /*border: 1px #000 solid;*/
    margin: 15px 0px 15px 0px;
    padding: 5px;
}

.leaflet-container {
    width: 100%;
    height: 100vh;
  }


